import { useState } from 'react'

export default function Selection({ position, index, sound }) {
  const [checked, setChecked] = useState(false)

  if (position === index + 1 && checked) {
    sound.sound.play()
  }

  const handleChange = () => {
    setChecked(!checked)
  }

  const handleClick = () => {
    if (!checked) {
      sound.sound.play()
    }
  }

  return (
    <td key={sound.sound.toString()} className={`selection ${checked ? 'highlight' : ''}`}>
      <input type="checkbox" onChange={handleChange} {...{ checked }} onClick={handleClick} />
    </td>
  )
}
